$sizes: 10px, 20px, 30px;

@each $size in $sizes {
  //   $size-name: ($size / 1px);
  //   $size-name: str-slice($size, 1, -3);
  //   $size-name: str-slice(#{$size}, 1, -3);
  $size-name: str-slice(#{$size}, 1, -3);

  .icon-#{$size-name} {
    font-size: $size;
    height: $size;
    width: $size;
  }
}
